import React, { useEffect, useContext } from "react";
import Input from "../../ui/Input";
import Select from "../../ui/Select";
import { Formik, Form } from "formik";
import { step_8_validationSchema as validationSchema } from "@/utils/schema";
import Cookies from "js-cookie";
import { useDebounce } from "@/app/hooks/useDebounce";
import axios from "axios";
import { loginContext } from "../../GlobalContext";

const Section = ({ children, className }) => {
  return (
    <div className={`bg-white rounded-lg shadow-lg p-4 ${className}`}>
      {children}
    </div>
  );
};

const Step_8 = ({ data, updateData }) => {
  const token = Cookies.get("rto-token");
  const debouncedData = useDebounce(data, 5000);
  const formId = Cookies?.get("formId");
  const { erroObj, setErrorObj } = useContext(loginContext);

  useEffect(() => {
    if (Object.keys(debouncedData).length > 0) {
      saveFormData(debouncedData);
    }
  }, [debouncedData]);

  useEffect(() => {
    scrollTo(0, 0);
    updateData({
      deliveryDate: new Date().toISOString().split("T")[0],
    })
  }, []);

  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + token,
    },
  };

  const saveFormData = async (formData) => {
    try {
      const payload = {
        formType: "step-8",
        form_id: formId || null,
        buyer_name: formData.buyerName,
        building_type: formData.buildingType,
        building_serial_number: formData.serialNumber,
        delivery_date: formData.deliveryDate,
        signature: formData.signature,
        directions: formData.directions,
      };

      const response = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}api/form/application-forms`,
        payload,
        config
      );
      if (response.status === 200) {
        console.log("Auto saved successfully");
      }
    } catch (error) {
      if (error.response) {
        console.log(error.response?.data?.message);
      } else {
        console.log("Error:", error.message);
      }
    }
  };

  const initialValues = {
    buyerName: "",
    buildingType: "",
    serialNumber: "",
    deliveryDate: "",
    signature: "",
    directions: "",
  };

  return (
    <Section>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={(values) => console.log(values)}
        validateOnBlur={true} // validate when field is touched
        validateOnChange={false} // optional, only validate on blur
      >
        {({ values, handleChange, handleBlur, errors, touched }) => (
          <Form className="space-y-4">
            <div className="max-w-4xl mx-auto sm:px-6 mb-10 text-lg">
              <div className="text-center mb-6 py-4 text-[#4b599b] underline">
                <h2 className="text-3xl font-bold mb-5">
                  Keens&apos; Buildings, INC.
                </h2>
                <h2 className="text-2xl font-bold">
                  Delivery Receipt and Acknowledgment
                </h2>
              </div>

              <div className="border-t border-gray-300 pt-4 mb-6"></div>

              {/* Buyer Acknowledgment Section */}

              <div className="mb-6">
                {touched.buyerName &&
                  errors.buyerName &&
                  !erroObj.buyerName && (
                    <div className="text-red-500 flex text-right text-sm">
                      {errors.buyerName}
                    </div>
                  )}
                {erroObj.buyerName && (
                  <div className="text-red-500 text-sm">
                    {erroObj.buyerName}
                  </div>
                )}
                <p className="flex flex-wrap items-center gap-1">
                  Buyer
                  <Input
                    className="max-w-96 m-1 inline-flex"
                    name={"buyerName"}
                    value={data.buyerName || ""}
                    onChange={(e) => {
                      handleChange(e);
                      updateData({ buyerName: e.target.value });
                      setErrorObj({ ...erroObj, buyerName: "" });
                    }}
                    onBlur={handleBlur}
                  />
                  I Acknowledge delivery of a
                  <Input
                    className="!w-fit m-1 inline-flex"
                    name={"buildingType"}
                    value={data.buildingType || ""}
                    onChange={(e) => {
                      handleChange(e);
                      setErrorObj({ ...erroObj, buildingType: "" });
                      updateData({ buildingType: e.target.value });
                    }}
                    onBlur={handleBlur}
                  />
                  {touched.buildingType &&
                    errors.buildingType &&
                    !erroObj.buildingType && (
                      <span className="text-red-500 flex text-right text-sm">
                        {errors.buildingType}
                      </span>
                    )}
                  {erroObj.buildingType && (
                    <span className="text-red-500 text-sm">
                      {erroObj.buildingType}
                    </span>
                  )}
                  Building serial #
                  <Input
                    className="!w-fit m-1 inline-flex"
                    name={"serialNumber"}
                    value={data.serialNumber || ""}
                    onChange={(e) => {
                      handleChange(e);
                      updateData({ serialNumber: e.target.value });
                      setErrorObj({ ...erroObj, serialNumber: "" });
                    }}
                    onBlur={handleBlur}
                  />
                  {touched.serialNumber &&
                    errors.serialNumber &&
                    !erroObj.serialNumber && (
                      <span className="text-red-500 flex text-right text-sm">
                        {errors.serialNumber}
                      </span>
                    )}
                  {erroObj.serialNumber && (
                    <span className="text-red-500 text-sm">
                      {erroObj.serialNumber}
                    </span>
                  )}
                  on the date
                  <Input
                    className="!w-fit m-1 inline-flex"
                    name={"deliveryDate"}
                    value={data.deliveryDate || ""}
                    type="date"
                    onChange={(e) => {
                      handleChange(e);
                      updateData({ deliveryDate: e.target.value });
                      setErrorObj({ ...erroObj, buildingSerialNumber: "" });
                    }}
                    onBlur={handleBlur}
                  />
                  {touched.deliveryDate &&
                    errors.deliveryDate &&
                    !erroObj.deliveryDate && (
                      <span className="text-red-500 flex text-right text-sm">
                        {errors.deliveryDate}
                      </span>
                    )}
                  {erroObj.deliveryDate && (
                    <span className="text-red-500 text-sm">
                      {erroObj.deliveryDate}
                    </span>
                  )}
                </p>

                <div className="mt-4">
                  <p className="font-semibold leading-tight">
                    I am satisfied and happy with the building, delivery and
                    set-up.
                  </p>
                </div>
              </div>

              <div className="border-t border-gray-300 pt-4 mb-6"></div>

              {/* Signature Section */}
              <div className="grid grid-cols-2 mb-6">
                <div className="">
                  <div className="flex items-center gap-2 mb-1">
                    <label>Signature:</label>
                  </div>
                  <Input
                    value={data.signature || ""}
                    name={"signature"}
                    onChange={(e) => {
                      handleChange(e);
                      updateData({ signature: e.target.value });
                      setErrorObj({ ...erroObj, signature: "" });
                    }}
                     onBlur={handleBlur}
                  />
                  {touched.signature && errors.signature && !erroObj.signature && (
                    <span className="text-red-500 flex text-right text-sm">
                      {errors.signature}
                    </span>
                  )}
                  {erroObj.signature && (
                    <span className="text-red-500 text-sm">
                      {erroObj.signature}
                    </span>
                  )}
                </div>
              </div>

              {/* Important Notices */}
              <div className="mb-6 space-y-3 font-semibold leading-tight">
                <p>
                  If your building is damaged or you have a problem with the
                  set-up and delivery call your dealer&apos;s once immediately.
                </p>
                <p>
                  If a return trip is requested to move the building, customer
                  will be subject to a minimum $500 fee.
                </p>
              </div>

              <div className="border-t border-gray-300 mb-8 mt-10"></div>

              {/* Directions Section */}
              <div className="mb-8">
                <label className="font-semibold text-lg mb-4 block text-center">
                  ****Please write directions to customer&apos;s property
                  below.***
                </label>
                <textarea
                  value={data.directions || ""}
                  name="directions"
                  onBlur={handleBlur}
                  onChange={(e) => {
                    handleChange(e);
                    updateData({ directions: e.target.value });
                    setErrorObj({ ...erroObj, directions: "" });
                  }}
                  placeholder="Write the directions..."
                  className="w-full p-3 border border-gray-300 rounded h-32 resize-vertical"
                />
                {touched.directions &&
                  errors.directions &&
                  !erroObj.directions && (
                    <span className="text-red-500 flex text-right text-sm">
                      {errors.directions}
                    </span>
                  )}
                {erroObj.directions && (
                  <span className="text-red-500 text-sm">
                    {erroObj.directions}
                  </span>
                )}
              </div>

              {/* Action Buttons */}
              <div className="flex flex-col flex-wrap  justify-center gap-4 mt-6">
                <button className="max-w-64 mx-auto bg-[#4b599b] text-white px-6 py-2 rounded hover:bg-blue-900 transition-colors">
                  Print Documents
                </button>
                <button className="max-w-64 mx-auto bg-[#4b599b] text-white px-6 py-2 rounded hover:bg-blue-900 transition-colors">
                  Save Documents
                </button>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </Section>
  );
};

export default Step_8;
